.cards-titles .title {
    margin: 0 2rem;
  }
  
  /* Underline styles */
 .cards-titles .title span {
    display: block;
    position: relative;
    padding: 0.2em 0;
    text-align: center;
  }
  
  /* Fade in */
  .cards-titles .title span::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.1em;
    background-color: #F05454;
    opacity: 0;
    transition: opacity 300ms, transform 300ms;
  }
  
  .cards-titles .title span:hover::after,
 .cards-titles .title span:focus::after {
    opacity: 1;
    transform: translate3d(0, 0.2em, 0);
  }

  .cards-titles .title .selected::after{
    opacity: 1;

  }

